<template>
  <div class="questionnaireFinish" :class="{themeL:bindThemeL}">
    <!-- 头部 -->
    <navigation-bar @onLeftClick="onLeftClick" :pageName="title">

    </navigation-bar>
    <!-- 问卷详情列表 -->
    <section class="list">
      <form id="question">
        <div class="questionnaireTitle">
          <div class="leftImg">
            <img :src="iconImg" class="iconImg">
          </div>
          <div class="rightCon">{{PAPER_NAME}}</div>
        </div>
        <div class="item" v-for="(item,index) in questionList" :key="index">

          <div class="questionHeader" v-if="item.questTypeFlag == 1 && item.selectTypeFlag == 1">Q{{item.sortNo}} :
            {{item.questName}} (单选)
          </div>
          <div class="questionHeader" v-if="item.questTypeFlag == 1 && item.selectTypeFlag == 0">Q{{item.sortNo}} :
            {{item.questName}} (多选)
          </div>
          <div class="questionHeader" v-if="item.questTypeFlag == 0">Q{{item.sortNo}} : {{item.questName}}</div>
          <questionnaire-item :questionnaireCon='item'>

          </questionnaire-item>
        </div>
      </form>
    </section>
  </div>
</template>

<script>
  import NavigationBar from '@/common/currency/NavigationBar.vue';
  import QuestionnaireItem from '@/common/servers/questionnaire/QuestionnaireItem.vue';
  import {getPaperDetail, getFinishQuestData} from '@/services/servers/question';
  import {Dialog} from 'vant';

  export default {
    name: 'questionnaireFinish',
    components: {
      NavigationBar,
      QuestionnaireItem
    },
    mounted() {
      this.bindThemeL = this.$curTheme == 0 ? false : true;
      this.params = this.$route.query
      console.log('【问卷】 入参=' + JSON.stringify(this.params))
      //this.PAPER_NAME = this.params.paperName
      this.init();
      console.log('.')
    },
    data() {
      return {
        bindThemeL: this.$curTheme == 0 ? false : true,
        title: '问卷详情',
        userInfo: {
          "ROOM_CODE": "123355",
          "OWNER_CODE": "2019103016101113758"
        },
        params: {},
        PAPER_NAME: '',
        list: [
          {'header': '555'},
          {'header': '666'}
        ],

        questionList: [],
        iconImg: require('@/assets/images/home/question/question.png'),

      }
    },
    methods: {
      onLeftClick() {
        this.$store.dispatch("changeActiveFlag", 1);
        this.$router.go(-1);
      },
      init() {
        this.getPaperDetail()
      },
      getPaperDetail() {
        let param = {
          publishCode: this.params.publishCode,
          paperCode: this.params.paperCode,
          roomCode: this.params.roomCode,
          userCode: this.params.userCode
        };
        console.log("【问卷】getPaperDetail: param = " + JSON.stringify(param))
        getPaperDetail(param).then(
          res => {
            if (res.resultCode === "0") {
              this.PAPER_NAME = res.data.paperName;
              this.questionList = res.data.questList;
            } else {
              console.log("【问卷】getPaperDetail: error msg = " + res.msg)
            }
          },
          err => {
            console.log("【问卷】getPaperDetail: error = " + err)
          })
      }

    }
  }
</script>

<style scoped lang="scss">
  .themeL.questionnaireFinish {
    .rightCon {
      font-size: 18px;
      line-height: 28px;
    }

    .questionHeader {
      font-size: 15px;
    }

    /deep/ .van-radio__label {
      font-size: 15px;
    }

    /deep/ .answer {
      font-size: 15px;
    }
  }

  .questionnaireFinish {
    width: 100%;
    height: 100%;
    display: flex;
    overflow: hidden;
    flex-direction: column;
    background-color: #f9f9f9
  }

  .questionnaireFinish .list {
    /* flex: 1; */
    overflow-y: auto;
    background-color: #ffffff;
    margin: 15px 15px 0 15px;
    box-shadow: 0 0 6px 0 rgba(219, 219, 219, 0.50);
    border-radius: 5px;
  }

  .questionnaireFinish .questionnaireTitle {
    width: calc(100% - 24px);
    /* height: 24px; */
    padding: 12px;
    display: flex;
  }

  .questionnaireFinish .leftImg {
    height: 28px;
    width: 28px;
    margin-right: 12px
  }

  .questionnaireFinish .leftImg img {
    display: block;
    width: 100%;
    height: 100%;
  }

  .questionnaireFinish .item {
    width: 100%;
    height: auto;

    border-top: 1px solid #f8f8f8
  }

  .questionnaireFinish .rightCon {
    flex: 1;
    /* height: 24px; */
    line-height: 24px;
    font-size: 14px;
    color: #333333;
    font-weight: bold;
    word-break: break-word;
  }

  .questionnaireFinish .questionHeader {
    width: calc(100% - 24px);
    /* height: 20px; */
    margin: 10px 12px 0 12px;
    padding-bottom: 6px;
    font-size: 14px;
  }

  #question {
    height: auto;
  }
</style>